}

.catalog-title {
  color: #1e2827;
  letter-spacing: -.02em;
  margin-bottom: 83px;
  font-size: 32px;
  font-weight: 600;
  line-height: 1.25;
}

.catalog-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 48px 24px;
  display: flex;
}

.catalog-list-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.image-container {
  width: 100%;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.image-container img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: block;
}

.overlay {
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: #1e2827;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease-in-out, transform .3s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  bottom: -100%;
  left: 0;
}

.image-container:hover .overlay {
  opacity: 1;
  transform: translateY(0);
}

.overlay-description {
  text-align: left;
  color: #fff;
  margin-top: 20px;
  padding: 15px 10px 5px;
}

.overlay-title {
  text-transform: uppercase;
  letter-spacing: -.02em;
  color: #fff;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 400;
