:root {
  --white: #fff;
  --darkgreen: #1e2827;
  --black: #141414;
}

.header {
  background-color: var(--black);
  align-items: center;
  display: flex;
}

.header-container {
  width: 100%;
  box-sizing: border-box;
  z-index: 2;
  justify-content: space-between;
  align-items: center;
  margin-top: 29px;
  display: flex;
}

.header-nav {
  align-items: center;
  display: flex;
}

.logo-text {
  letter-spacing: -.03em;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.logo-link {
  color: #fff;
  align-items: center;
  gap: 4px;
  display: flex;
}

.logo-link img {
  width: 14px;
  height: 14px;
  margin-bottom: 4px;
}

.menu {
  display: none;
}

.header-address-container:hover, .header-address-container:focus {
  fill: var(--white);
  background-color: #25312f;
  border: 1px solid #fff;
}

.header-address {
  display: none;
}

.menu-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  padding: 0;
  display: flex;
}

.modal-menu-window {
  z-index: 100;
  height: 100vh;
  width: 100%;
  background-color: var(--darkgreen);
  color: var(--white);
  visibility: hidden;
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.modal-menu-window.is-open {
  visibility: visible;
}

.menu-close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.modal-header-container {
  width: 335px;
  justify-content: space-between;
  align-items: center;
  margin-top: 29px;
  padding: 0;
  display: flex;
}

.modal-header-nav {
  justify-content: center;
  display: flex;
}

.modal-menu-container {
  justify-content: center;
  margin-top: 200px;
  display: flex;
}

.modal-menu {
  flex-direction: column;
  align-items: center;
  gap: 14px;
  display: flex;
}

.modal-menu-link {
  color: var(--white);
  text-align: center;
  font-weight: 400;
  transition: color .3s;
  position: relative;
}

.modal-menu-link:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  border-radius: 2px;
  transition: transform .3s;
  position: absolute;
  bottom: -1px;
  left: 0;
  transform: scaleX(0);
}

.modal-menu-link:hover:after {
  transform: scaleX(1);
}

.modal-menu-link:hover, .modal-menu-link:focus {
  color: rgba(255, 255, 255, .7);
}

.modal-social-media-container {
  background-color: #1a2221;
  margin-top: auto;
  padding: 40px 0;
}

.modal-social-media-title {
  text-align: center;
  letter-spacing: -.03em;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.modal-social-media {
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
  display: flex;
}

.modal-social-media-link {
  fill: var(--darkgreen);
  width: 36px;
  height: 36px;
  background-color: #121717;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 15%;
  justify-content: center;
  align-items: center;
  transition: background-color .3s, border .3s;
  display: flex;
}

.modal-social-media-link:hover, .modal-social-media-link:focus {
  fill: var(--white);
  background-color: #25312f;
  border: 1px solid #fff;
}

@media screen and (min-width: 768px) {
  .header-container {
    color: #fff;
    margin-top: 20px;
  }

  .logo-link img {
    width: 19px;
    height: 19px;
    margin-bottom: 3px;
  }

  .logo-text {
    box-sizing: border-box;
    font-size: 15px;
    line-height: 15.37px;
  }

  .header-nav {
    gap: 80px;
  }

  .menu {
