    display: flex;
  }

  .menu-link {
    color: #fff;
    margin: 0;
    transition: color .3s;
    position: relative;
  }

  .menu-link:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #fff;
    border-radius: 2px;
    transition: transform .3s;
    position: absolute;
    bottom: -1px;
    left: 0;
    transform: scaleX(0);
  }

  .menu-link:hover:after {
    transform: scaleX(1);
  }

  .menu-link:hover, .menu-link:focus {
    color: rgba(255, 255, 255, .7);
  }

  .header-address-container {
    width: 159px;
    height: 48px;
    background-color: var(--darkgreen);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 8px;
    justify-content: center;
    margin-top: 0;
    transition: background-color .3s, border .3s;
    display: flex;
  }

  .header-address {
    justify-content: center;
    align-items: center;
    gap: 40px;
    font-size: 9px;
